.Navbar-container {
  padding: 2rem 2rem;
  padding-bottom: 0rem;
  border: .5rem solid #2A363B;
  border-bottom: none;
}
.Navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  vertical-align: middle;
  padding: 0rem 0rem;
  max-width: 1000px;
  margin: auto;
}
.Navbar-link-container {
  margin-top: auto;
  color: #2A363B;
}
.Navbar-link {
  line-height: 0rem;
  font-family: 'Nunito', sans-serif;
  color: #2A363B;
  margin-right: 0rem;
  margin-left: 1rem;
  font-size: 1.45rem;
  font-weight: 300;
}
.Navbar-link a {
  color: #2A363B;
}

.Navbar-profile {
  /* box-shadow: 0px 25px 20px -9px rgba(0,0,0,0.1); */
  overflow: hidden;
  border-radius: 5px;
  width: 75px;
  height: 75px;
  margin-right: auto;
  flex-shrink: 0;
}
.Navbar-profile img {
  max-height: 90px;
  filter: contrast(.8) grayscale(1) brightness(1.3);
}

@media only screen and (max-width: 500px) {
  .Navbar-link {
    font-size: 1.25rem;
    /* font-weight: 500; */
  }
}
