@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Oleo+Script|Signika|Nunito:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar-container {
  padding: 2rem 2rem;
  padding-bottom: 0rem;
  border: .5rem solid #2A363B;
  border-bottom: none;
}
.Navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  vertical-align: middle;
  padding: 0rem 0rem;
  max-width: 1000px;
  margin: auto;
}
.Navbar-link-container {
  margin-top: auto;
  color: #2A363B;
}
.Navbar-link {
  line-height: 0rem;
  font-family: 'Nunito', sans-serif;
  color: #2A363B;
  margin-right: 0rem;
  margin-left: 1rem;
  font-size: 1.45rem;
  font-weight: 300;
}
.Navbar-link a {
  color: #2A363B;
}

.Navbar-profile {
  /* box-shadow: 0px 25px 20px -9px rgba(0,0,0,0.1); */
  overflow: hidden;
  border-radius: 5px;
  width: 75px;
  height: 75px;
  margin-right: auto;
  flex-shrink: 0;
}
.Navbar-profile img {
  max-height: 90px;
  -webkit-filter: contrast(.8) grayscale(1) brightness(1.3);
          filter: contrast(.8) grayscale(1) brightness(1.3);
}

@media only screen and (max-width: 500px) {
  .Navbar-link {
    font-size: 1.25rem;
    /* font-weight: 500; */
  }
}

.Title h1 {
  font-size: 5rem;
}
.Title-border {
  background-color: #2A363B;
}
.Title-container {
  border-left: .5rem solid #2A363B;
  border-right: .5rem solid #2A363B;
  padding: 2rem;
  padding-bottom: 6rem;
  /* min-height: 300px; */
  max-height: 800px;
}
.Title {
  min-height: 500px;
  max-width: 1000px;
  padding: 3rem 1rem;
  margin: auto;
  border: 1px solid #2A363B;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Title-name {
  text-align: center;
  margin-bottom: 3rem;
  color: #2A363B;
}

.Title-blurb {
  color: #2A363B;
  font-family: 'Nunito', sans-serif;
  font-size: 1.25rem;
  line-height: 2rem;
  margin: 0 20%;
  text-align: center;
}

@media only screen and (max-width: 650px) {
  .Title h1 {
    font-size: 3rem;
  }
  .Title h2 {
    font-size: 1.5rem;
  }
  .Title-blurb {
    font-size: 1.25rem;
    line-height: 2rem;
  }
  .Title {
    padding-bottom: 5rem;
    min-height: auto;
  }
}

@media only screen and (max-width: 500px) {
  .Title-container {
    padding: 4rem 2rem;
  }
  .Title {
    margin: 0;
    border: none;
    border-top: 1px solid #2A363B;
    border-bottom: 1px solid #2A363B;
    padding: 3rem 0rem;
    text-align: left;
    display: inline-block;
  }
  .Title-name {
    margin: 1rem 0;
    font-size: 2rem;
    text-align: left;
  }
  .Title-blurb {
    font-size: 1.1rem;
    margin: 0;
    text-align: left;
  }
}

.Bubble h2 {
  font-weight: 400;
  font-size: 1.5rem;
}

.Bubble p {
    font-size: 18px;
    padding-top: 1rem;
    line-height: 2.2rem;
    color: #555555;
}
.Bubble-image-container {
  padding-bottom: 3.5rem;
}
.Bubble-container.left, .Bubble-container.right {
  margin: auto;
  padding: 2rem 0rem;
}
.Bubble-container.left {
  background-color: #2A363B;
}

.Bubble-container.left p,
.Bubble-container.left h1,
.Bubble-container.left h2 {
  color: #FAFAFA;
}

.Bubble-container.right {
  background-color: #FAFAFA;
}


.Bubble {
  max-width: 1000px;
  min-height: 550px;
  margin: auto;
  border: 1px solid #2A363B;
  padding: 1rem;
  padding-top: 4rem;
  padding-bottom: 4rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}

.Bubble.right {
  border: none;
  /* border-top: 1px solid #2A363B;
  border-bottom: 1px solid #2A363B; */
  padding: 7rem 2rem;
  color: #2A363B;
}
.Bubble.left {
  border: none;
  /* border-top: 1px solid #FAFAFA;
  border-bottom: 1px solid #FAFAFA; */
  color: #FAFAFA;
  padding: 7rem 2rem;
}

