.Bubble-image {
  box-shadow: 0px 25px 20px -9px rgba(0,0,0,0.1);
  overflow: hidden;
  border-radius: 2px;
  width: 200px;
  height: 100px;
  margin-right: 5rem;
  margin-bottom: 1rem;
  margin-left: 10rem;
  flex-shrink: 0;
}
.Bubble-image.right {
  margin-right: 5rem;
  margin-left: 0rem;
}
.Bubble-image.left {
  margin-right: 5rem;
  margin-left: 0;
}

.Bubble img {
  -webkit-filter: grayscale(.75) brightness(1.05);
          filter: grayscale(.75) brightness(1.05);
  max-height: 150px;
}
.Bubble-info {
  max-width: 500px;
  flex-grow: auto;
}
.Bubble-info.right {
  /* margin-left: 10rem; */
}
.Bubble-image-links {
  text-align: right;
  padding-right: 5rem;
}


@media only screen and (max-width: 925px) {
  .Bubble-container {
    padding: 3rem 2rem;
    padding-top: 0;
    padding-bottom: 0;
  }

  .Bubble-image {
      margin-left: 0rem;
      margin-right: 3rem;
  }
  .Bubble-image.right {
      /* margin-left: 3rem; */
      /* margin-right: 0rem; */
  }
  .Bubble-info.right {
      margin-left: 0rem;
      margin-right: 0rem;
  }
}
@media only screen and (max-width: 600px) {
  .Bubble-container {
    margin: auto;
    padding: 2rem;
    /* border: 1px solid blue; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
  }
  .Bubble {
    width: 100%;
    text-align: left;
    padding: 0;
  }
  .Bubble.left p,
  .Bubble.left h1,
  .Bubble.left h2
   {
    text-align: left;
  }
  .Bubble.right p,
  .Bubble.right h1,
  .Bubble.right h2
   {
    text-align: left;
  }

  .Bubble-image-container {
    display: none;
  }

  .Bubble-image {
    margin-right: 0;
    margin-bottom: 2rem;
    box-shadow: none;
    display: none;
  }

  .Bubble-image.right {
    display: none;
    margin-right: 0;
    margin-bottom: 2rem;
    box-shadow: none;
  }
  .Bubble-info {
    text-align: center;
  }


}

.Contact-container {
  border-left: .5rem solid #2A363B;
  border-right: .5rem solid #2A363B;
  padding: 4rem 0;
  padding-bottom: 3rem;
}
.Contact {
  margin: auto;
  text-align: center;
  border-bottom: 1px solid #2A363B;
  max-width: 800px;
  padding: 3rem 5rem;
  padding-bottom: 5rem;
}
.Contact p {
  font-size: 18px;
  padding-top: 1rem;
  line-height: 2.2rem;
  color: #2A363B;
}
@media only screen and (max-width: 925px) {
  .Contact {
    border-top: none;
    border-bottom: none;
    padding: 0 2rem;
  }
}
@media only screen and (max-width: 600px) {
  .Contact {
    text-align: left;
    padding: 0 2rem;
  }
}

.Flourish {
  position: relative;
  margin: auto;
  border: none;
}

.top {
  border: .5rem solid #2A363B;
  border-top: none;
  border-bottom: none;
}
.Flourish > * {
  width: 75px;
}

.b-top {
  margin:auto;
  position: relative;
  top: 25px;
  right: 2px;
  box-sizing: border-box;
  border-top: 20px #2A363B solid;
  /* border-left: 15px #2A363B solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.b-bottom {
  position: relative;
  top: 15px;
  right: 20px;
  margin: auto;
  box-sizing: border-box;
  border-bottom: 20px #FAFAFA solid;
  /* border-right: 15px #FAFAFA solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.Flourish-2 {
  position: relative;
  margin: auto;
  border: none;
}
.Flourish-2.top {
  border: .5rem solid #2A363B;
  border-top: none;
  border-bottom: none;
}
.Flourish-2 > * {
  width: 75px;
}

.b-top-2 {
  margin:auto;
  position: relative;
  top: -20px;
  right: 7px;

  box-sizing: border-box;
  border-top: 20px #2A363B solid;
  border-right: 20px #2A363B solid;
  /* border-left: 15px #2A363B solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.b-bottom-2 {
  position: relative;
  top: -25px;
  right: 20px;

  margin:auto;
  box-sizing: border-box;
  border-bottom: 20px #FAFAFA solid;
  /* border-right: 15px #FAFAFA solid; */
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

