.FooterComponent-container {
  display: flex;
  border: .5rem solid #2A363B;
  border-top: none;
}
.FooterComponent {
  margin: auto;
  list-style: none;
  text-align: center;
  color: #2A363B;
  font-size: .85rem;
  padding:0;
  padding-bottom: 2rem;
}

@media only screen and (max-width: 600px) {
  .FooterComponent {
    text-align: left;
    margin: 0;
    padding: 2rem;
  }
}

